import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  useFinanceDashboardMaster,
  useFinanceTabController,
} from "../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../Ui/Border/BorderBottom";
import FormToggle from "../../../StaffTab/components/DividerTab/MainContent/StaffForm/FormToggle";
import style from "../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import NotifyEvent from "../../Utils/Snackbar/NotifyEvent";
const InstituteFinanceTabController = ({ financeId }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    all_students: true,
    bank_details: true,
    data_export: true,
    deposits: true,
    exemption: true,
    expenses: true,
    fee_statistics: true,
    finance_mods: true,
    incomes: true,
    pending_fees: true,
    scholarships: true,
    submit_request: true,
    transaction_history: true,
    fees_structure: true,
    admission_offline_verification: true,
    admission_pending_fees: true,
    admission_excess_fees: true,
  });
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });
  const [financeTabController] = useFinanceTabController();
  const { financeDashboardMaster } = useFinanceDashboardMaster({
    data: {
      fid: financeId,
      mid: "",
    },
    skip: !financeId,
  });

  useEffect(() => {
    if (financeDashboardMaster?.finance?.tab_manage) {
      setState({
        all_students: financeDashboardMaster?.finance?.tab_manage?.all_students,
        bank_details: financeDashboardMaster?.finance?.tab_manage?.bank_details,
        data_export: financeDashboardMaster?.finance?.tab_manage?.data_export,
        deposits: financeDashboardMaster?.finance?.tab_manage?.deposits,
        exemption: financeDashboardMaster?.finance?.tab_manage?.exemption,
        expenses: financeDashboardMaster?.finance?.tab_manage?.expenses,
        fee_statistics:
          financeDashboardMaster?.finance?.tab_manage?.fee_statistics,
        finance_mods: financeDashboardMaster?.finance?.tab_manage?.finance_mods,
        incomes: financeDashboardMaster?.finance?.tab_manage?.incomes,
        pending_fees: financeDashboardMaster?.finance?.tab_manage?.pending_fees,
        scholarships: financeDashboardMaster?.finance?.tab_manage?.scholarships,
        submit_request:
          financeDashboardMaster?.finance?.tab_manage?.submit_request,
        transaction_history:
          financeDashboardMaster?.finance?.tab_manage?.transaction_history,

        admission_pending_fees:
          financeDashboardMaster?.finance?.tab_manage?.admission_pending_fees,
        admission_offline_verification:
          financeDashboardMaster?.finance?.tab_manage
            ?.admission_offline_verification,
        admission_excess_fees:
          financeDashboardMaster?.finance?.tab_manage?.admission_excess_fees,
        fees_structure: true,
      });
    }
  }, [financeDashboardMaster?.finance?.tab_manage]);

  const onSubmitFormHandler = (field, value) => {
    const updatedObj = { ...state };
    for (let obj in updatedObj) {
      if (obj === field) {
        updatedObj[obj] = value;
      }
    }
    financeTabController({
      fid: financeId,
      tabManage: { tab_manage: updatedObj },
    })
      .then(() => {
        setSnackbar({
          msg: "Updated",
          open: true,
          variant: "success",
        });
      })
      .catch({});
    setState((prev) => ({
      ...prev,
      [field]: value,
    }));
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("tab_controller")}</h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("all_students")}
              checkedToggle={state.all_students}
              onToggleHandler={(e) =>
                onSubmitFormHandler("all_students", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("bank_details")}
              checkedToggle={state.bank_details}
              onToggleHandler={(e) =>
                onSubmitFormHandler("bank_details", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("data_export")}
              checkedToggle={state.data_export}
              onToggleHandler={(e) =>
                onSubmitFormHandler("data_export", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
          <BorderBottom />
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("deposits")}
              checkedToggle={state.deposits}
              onToggleHandler={(e) =>
                onSubmitFormHandler("deposits", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("exemption")}
              checkedToggle={state.exemption}
              onToggleHandler={(e) =>
                onSubmitFormHandler("exemption", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />

            <FormToggle
              labelHeading={t("expenses")}
              checkedToggle={state.expenses}
              onToggleHandler={(e) =>
                onSubmitFormHandler("expenses", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
          <BorderBottom />
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("fee_statistics")}
              checkedToggle={state.fee_statistics}
              onToggleHandler={(e) =>
                onSubmitFormHandler("fee_statistics", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />

            <FormToggle
              labelHeading={t("finance_mods")}
              checkedToggle={state.finance_mods}
              onToggleHandler={(e) =>
                onSubmitFormHandler("finance_mods", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("incomes")}
              checkedToggle={state.incomes}
              onToggleHandler={(e) =>
                onSubmitFormHandler("incomes", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
          <BorderBottom />
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("admission_fees")}
              checkedToggle={state.admission_pending_fees}
              onToggleHandler={(e) =>
                onSubmitFormHandler("admission_pending_fees", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("offline_payment_verification")}
              checkedToggle={state.admission_offline_verification}
              onToggleHandler={(e) =>
                onSubmitFormHandler(
                  "admission_offline_verification",
                  e.target.checked
                )
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />

            <FormToggle
              labelHeading={t("excess_fees_refund")}
              checkedToggle={state.admission_excess_fees}
              onToggleHandler={(e) =>
                onSubmitFormHandler("admission_excess_fees", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
          <BorderBottom />
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("other_fees")}
              checkedToggle={state.pending_fees}
              onToggleHandler={(e) =>
                onSubmitFormHandler("pending_fees", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("scholarship")}
              checkedToggle={state.scholarships}
              onToggleHandler={(e) =>
                onSubmitFormHandler("scholarships", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />

            <FormToggle
              labelHeading={t("submit_request")}
              checkedToggle={state.submit_request}
              onToggleHandler={(e) =>
                onSubmitFormHandler("submit_request", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
          <BorderBottom />
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("transaction_history")}
              checkedToggle={state.transaction_history}
              onToggleHandler={(e) =>
                onSubmitFormHandler("transaction_history", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default InstituteFinanceTabController;
